import { Project } from '@const/projects';
import Card from '@modules/common/components/widgets/Card';
import React from 'react';
import ProjectItem from '../ProjectItem';

import './styles.scss';

interface Props {
  projects: Project[]
}

const ProjectsList:React.FC<Props> = ({ projects }) => {

  return (
    <div className='projects-list'>
      {
        projects.map((p, i) => (
          <ProjectItem key={p.title} {...p} />
        ))
      }
    </div>
  )
};

export default ProjectsList;