import { Project } from '@const/projects';
import React from 'react';
import { useWebPSupportCheck } from "react-use-webp-support-check";

import './styles.scss';

const ProjectItem:React.FC<Project> = ({ title, tags, description, img, links }) => {
  const supportsWebP = useWebPSupportCheck();
  return (
    <div className='project'>
      <div className='project--header'>
        <img src={(supportsWebP && !!img.webp) ? img.webp : img.png} className='project--image' />
      </div>
      <div className='project--body'>
        <div className='project--content'>
          <h3 className='project--title'>{title}</h3>
          <ul className='project--tags'>
            {tags.map(t => <li key={t}>{t}</li>)}
          </ul>
          <p className='project--desc'>{description}</p>
        </div>
        {
          !!links && (
            <div className='project--links'>
              {!!links.demo && <a target={'_blank'} href={links.demo}>Демо</a>}
              {!!links.github && <a target={'_blank'} href={links.github}>Репозиторий</a>}
            </div>
          )
        }
      </div>
    </div>
  )
};

export default ProjectItem;