import React from 'react';
import { NavLink, NavLinkProps } from 'react-router-dom';

import Icon from '@modules/common/components/ui/Icon';
import { IconName } from '@modules/common/components/ui/Icon/Icon';

import './styles.scss';

interface Props extends NavLinkProps {
  name: IconName;
  text: string;
  isOpen: boolean;
}

const NavigationLink:React.FC<Props> = ({ name, text, isOpen, ...linkProps }) => {
  return (
    <NavLink title={text} className={({isActive}) => 'navigation__link' + (isActive ? ' navigation__link-active' : '')} {...linkProps}>
      <i></i>
      <div className={`navigation__link__content ${isOpen ? 'navigation__link__content-open' : ''}`}>
        <Icon name={name} />
        <p>{text}</p>
      </div>
      <i></i>
    </NavLink>
  )
};

export default NavigationLink;