import React, { useMemo } from 'react';

import './styles.scss';

import { ReactComponent as About } from '@assets/lnr-graduation-hat.svg';
import { ReactComponent as Skills } from '@assets/lnr-rocket.svg';
import { ReactComponent as Projects } from '@assets/lnr-briefcase.svg';
import { ReactComponent as Pet } from '@assets/lnr-paw.svg';
import { ReactComponent as Phone } from '@assets/lnr-tablet.svg';
import { ReactComponent as Chevron } from '@assets/lnr-chevron-left-circle.svg';

import { ReactComponent as Docker } from '@assets/chart/docker.svg';
import { ReactComponent as Git } from '@assets/chart/git.svg';
import { ReactComponent as GraphQL } from '@assets/chart/graphql.svg';
import { ReactComponent as Html } from '@assets/chart/html.svg';
import { ReactComponent as JavaScript } from '@assets/chart/javascript.svg';
import { ReactComponent as Next } from '@assets/chart/next.svg';
import { ReactComponent as ReactIcon } from '@assets/chart/react.svg';
import { ReactComponent as TypeScript } from '@assets/chart/typescript.svg';


const ICONS = {
  about: About,
  skills: Skills,
  projects: Projects,
  pet: Pet,
  phone: Phone,
  chevron: Chevron,
  docker: Docker,
  git: Git,
  graphql: GraphQL,
  html: Html,
  javascript: JavaScript,
  next: Next,
  react: ReactIcon,
  typescript: TypeScript
} as const;

export type IconName = keyof typeof ICONS;

interface Props extends React.SVGProps<SVGSVGElement> {
  name: IconName;
  colorNative?: boolean;
}

const Icon:React.FC<Props> = ({ name, colorNative = false, ...svgProps }) => {

  const Component = useMemo(() => {
    return ICONS[name];
  }, [name]);

  return (
    <Component className={`icon ${colorNative ? 'icon-colorNative' : ''}`} {...svgProps} />
  )
};

export default Icon;