import { Route, Routes, useLocation } from 'react-router-dom';
import About from '@modules/about/screen/About';
import Navigation from '@modules/common/components/widgets/Navigation';
import Projects from '@modules/projects/screen/Projects';
import { useEffect } from 'react';

function App() {
  const { pathname } = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname])

  return (
    <div className='screen'>
      <Navigation />
      <div className='screen--content'>
        <Routes>
          <Route path='/' element={<About />} />
          <Route path='/projects' element={<Projects title='Портфолио' />} />
        </Routes>
      </div>
    </div>
  );
}

export default App;
