import React, { PropsWithChildren } from 'react';

import './styles.scss';

interface Props {
  yearFrom: number;
  yearTo?: number;
  name: string;
}

const EducationItem:React.FC<PropsWithChildren<Props>> = ({ yearFrom, yearTo, name, children }) => {

  return (
    <div className='education--item'>
      <p className='education--dates'>
        {yearFrom}-{yearTo ?? 'н.в.'}
      </p>
      <div className='education--description'>
        <p className='education--name'>
          {name}
        </p>
        {children}
      </div>
    </div>
  )
};

export default EducationItem;