import React, { useLayoutEffect } from 'react';
// @ts-ignore
import BubbleUI from "react-bubble-ui";
import "react-bubble-ui/dist/index.css";
import BubbleChild from './BubbleChild';

import './styles.scss';

const options = {
  size: 180,
  minSize: 30,
  gutter: 12,
  provideProps: true,
  numCols: 4,
  fringeWidth: 180,
  yRadius: 130,
  xRadius: 220,
  cornerRadius: 50,
  showGuides: false,
  compact: true,
  gravitation: 5
}

const skills = [
  { label: 'JavaScript' },
  { label: 'React Hook Form' },
  { label: 'Docker' },
  { label: 'Redux' },
  { label: 'TypeScript' },
  { label: 'Next.js' },
  { label: 'Styled components' },
  { label: 'GraphQL' },
  { label: 'Apollo Client' },
  { label: 'React' },
  { label: 'React-jss' },
  { label: 'REST' },
  { label: 'SWR' },
  { label: 'Prisma' },
];

const SkillsBubbles:React.FC = () => {

  useLayoutEffect(() => {
    const bubbles = document.querySelector('._2MD0k') as HTMLElement;

    if (!bubbles) return;

    const dragspeed = 2;
    let isDown = false;
    let startX: number;
    let startY: number;
    let scrollLeft: number;
    let scrollTop: number;

    const onMouseDown = (e: MouseEvent) => {
      isDown = true;
      bubbles.classList.add('active');
      startX = e.pageX - bubbles.offsetLeft;
      startY = e.pageY - bubbles.offsetTop;
      scrollLeft = bubbles.scrollLeft;
      scrollTop = bubbles.scrollTop;
    }

    const onMouseLeave = () => {
      isDown = false;
      bubbles.classList.remove('active');
    }

    const onMouseUp = () => {
      isDown = false;
      bubbles.classList.remove('active');
    }

    const onMouseMove = (e: MouseEvent) => {
      if(!isDown) return;
      e.preventDefault();
      const x = e.pageX - bubbles.offsetLeft;
      const y = e.pageY - bubbles.offsetTop;
      const walk = (x - startX) * dragspeed;
      const topwalk = (y - startY) * dragspeed;
      bubbles.scrollLeft = scrollLeft - walk;
      bubbles.scrollTop = scrollTop - topwalk;
    }
    
    bubbles.addEventListener('mousedown', onMouseDown as EventListener);
    bubbles.addEventListener('mouseleave', onMouseLeave);
    bubbles.addEventListener('mouseup', onMouseUp);
    bubbles.addEventListener('mousemove', onMouseMove as EventListener);

    return () => {
      bubbles.removeEventListener('mousedown', onMouseDown);
      bubbles.removeEventListener('mouseleave', onMouseLeave);
      bubbles.removeEventListener('mouseup', onMouseUp);
      bubbles.removeEventListener('mousemove', onMouseMove);
    }
  }, []);

  return (
    <BubbleUI options={options} className="myBubbleUI">
      {skills.map((s, i) => <BubbleChild key={i}>{s.label}</BubbleChild>)}
    </BubbleUI>
  )
};

export default SkillsBubbles;