import React, { ButtonHTMLAttributes } from 'react';
import Ink from 'react-ink';

import './styles.scss';

interface Props extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  color?: 'black' | 'white',

}

const Button:React.FC<Props> = ({ color = 'black', className, children, ...buttonProps }) => {
  return (
    <button className={`button button-${color} ${className}`} {...buttonProps}>
      {children}
      <Ink />
    </button>
  )
};

export default Button;